  return (
    <>
      <MapContainer
        ref={map}
        id="mapid"
        center={[0, 20]}
        zoom={3.5}
        zoomSnap={0.25}
        /*  maxBounds={[
        [[ -18.0701, 21.9999], [-13.4551, 33.7049]],
      ]} */
        scrollWheelZoom={true}
        style={{
          height: "100%",
          width: "100%",
          position: "fixed",
        }}
        zoomControl={false}
        whenReady={(map) => {
          map.target.on("click", function (e) {
            console.log("hice click", e);
          });
        }}
      >

        <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
        {checkMalawi &&
        <>
        <GeoJSON data={hola.features[3].geometry} />
        {malawi.map((dat, index) => (
            <>
              
              <Marker index={index} position={[dat.latitude, dat.longitude]}>
                <Tooltip direction="top" offset={[0, -30]}>
                  Name: {dat.name} <br />
                  Ohers: {dat.others}
                  <br />
                  <br />
                </Tooltip>
              </Marker>
            </>
          ))}
        </>

          }
        {checkTanzania &&
        <>
        <GeoJSON data={hola.features[5].geometry} />
        {tanzania.map((dat, index) => (
            <>
              
              <Marker index={index} position={[dat.latitude, dat.longitude]}>
                <Tooltip direction="top" offset={[0, -30]}>
                  Name: {dat.name} <br />
                  Ohers: {dat.others}
                  <br />
                  <br />
                </Tooltip>
              </Marker>
            </>
          ))}
        </>

          }
        {markers.map((dato, index) => (
          <Marker key={index} position={[dato.litude, dato.longitude]}>
            <Tooltip direction="top" offset={[0, -30]}>
              <div>
                <h3>{dato.name}</h3>

                {dato.others.length > 0 && (
                  <p>{dato.others.map((other) => `${other} `)}</p>
                )}
                {dato.__parsed_extra?.length > 0 && (
                  <p>{dato.__parsed_extra.map((extra) => `${extra} `)}</p>
                )}
              </div>
            </Tooltip>
          </Marker>
        ))}
        {checkZambia &&
        <>
        <GeoJSON data={hola.features[14].geometry} />
        {zambia.map((dat, index) => (
            <>
              
              <Marker index={index} position={[dat.latitude, dat.longitude]}>
                <Tooltip direction="top" offset={[0, -30]}>
                  Name: {dat.name} <br />
                  Ohers: {dat.others}
                  <br />
                  <br />
                </Tooltip>
              </Marker>
            </>
          ))}
        </>

          }
        <Marker position={[-69.9962, 0.5594]}></Marker>
        <LayersControl position="topright" className="mt-5">
          <LayersControl.Overlay name="Zambia">
            <TileLayer
              url=""
              eventHandlers={{
                add: (e) => {
                  console.log("Added Layer:", e.target);
                  console.log("added zambia");
                  setCheckZambia(true);
                  map.current.flyToBounds(
                    [
                      [-18.0701, 21.9999],
                      [-13.4551, 33.7049],
                    ],
                    { padding: [150, 150] }
                  );
                },
                remove: (e) => {
                  console.log("Removed layer:", e.target);
                  console.log("removed zambia");
                  setCheckZambia(false);
                },
              }}
            />
          </LayersControl.Overlay>

          <LayersControl.Overlay name="Tanzania">
            <TileLayer
              url=""
              eventHandlers={{
                add: (e) => {
                  console.log("Added Layer:", e.target);
                  console.log("added tanzania");
                  setCheckTanzania(true);
                  map.current.flyToBounds(
                    [
                      [-11.7453, 29.3326],
                      [-0.99, 40.4676],
                    ],
                    { padding: [150, 150] }
                  );
                },
                remove: (e) => {
                  console.log("Removed layer:", e.target);
                  console.log("removed tanzania");
                  setCheckTanzania(false);
                },
              }}
            />
          </LayersControl.Overlay>
          <LayersControl.Overlay name="Malawi">
            <TileLayer
              url=""
              eventHandlers={{
                add: (e) => {
                  console.log("Added Layer:", e.target);
                  console.log("added malawi");
                  setCheckMalawi(true);
                  map.current.flyToBounds(
                    [
                      [-17.135, 32.6733],
                      [-9.3681, 35.922],
                    ],
                    { padding: [150, 150] }
                  );
                },
                remove: (e) => {
                  console.log("Removed layer:", e.target);
                  console.log("removed malawi");
                  setCheckMalawi(false);
                },
              }}
            />
          </LayersControl.Overlay>
        </LayersControl>
        //{" "}
      </MapContainer>
    </>
  );
